import React from 'react';
import Role from './Elements/Role';

class ChoiceRole extends React.Component {
    constructor(props) {
        super(props)
        this.state = {
            show: true,
            info_player: [
                {
                    text_recommend: 'Recomendado',
                    player: {
                        name: "Los Santos",
                        text: 'Marcha de su país en avión hacia la gran ciudad de Los Santos, con las cosas necesarias que tuvo tiempo de recoger, a partir de ahora queda marcar un nuevo camino'
                    }
                },
                {
                    text_recommend: '',
                    player: {
                        name: "Kayo-Periko",
                        text: 'Al llegar en avión a la isla Kayo-Perico, con ropa limpita, algo de dinero y un teléfono, ¡están abiertas todas las carreteras para conquistar este mundo!'
                    }
                },
                {
                    text_recommend: '',
                    player: {
                        name: "Oh, desde cero",
                        text: 'Despertando en las cercanías del condado de Blein, con la vieja ropa sucia y sin nada en el bolsillo, tendrá que volver a la vida normal. Lo bueno es que ya es un ciudadano estadounidense, lo que facilitará su futuro camino.'
                    }
                }
            ]
        }
    }
    selectChar(index) {
        mp.trigger('client:events:custom:choiceRole', index) // eslint-disable-line
    }
    componentDidCatch(error, errorInfo) {
        mp.trigger('client:ui:debug', 'ChoiceRole.jsx', error, errorInfo); // eslint-disable-line
    }

    render() {
        this.selectChar(0);
        return (0);
    }
}

export default ChoiceRole;
