import React from 'react'
import '../css/faq.css'
import BigButton from '../uikit/BigButton'
import Question from '../uikit/Question'
import { useState } from 'react'

const Faq = ({ onChangePage }) => {

    const questions = [
        {
            text: "¿Cómo empezar a jugar?",
            answer: `
                <p class="accountmenu__content__cards__answer__text">
                Lo primero que debe hacer es llegar al edificio del gobierno y comenzar su viaje.<br><br>
                Abra la pestaña "Misiones" y vaya a la línea de búsqueda "Primeros pasos".<br><br>
                </p>
                <ol>
                <li class="accountmenu__content__cards__answer__li">Nuestra página web: ciudadlatina.net</li>
                </ol>
            `
        },
        {
            text: "Cómo funciona el programa de referidos?",
            answer: `
                <p class="accountmenu__content__cards__answer__text">
                Después de que su amigo ingrese su apodo en el campo al registrar un personaje (Espacio por ejemplo: Alexey Navalniy), recibirá una bonificación en forma de Punto de bonificación en su cuenta personal. Gracias a estos puntos de bonificación, puede utilizar los servicios de donación o cambiarlos por moneda del juego. Después de que tu amigo juegue 24 horas en el servidor, recibirá un bono de $20 000 y 100 pb, y tú recibirás 500 pb en la cuenta de tu personaje.<br><br>
                Se puede ver una lista de todas sus referencias en el sitio, en las estadísticas de caracteres<br><br>
                </p>
                <ol>
                <li class="accountmenu__content__cards__answer__li">Nuestra página web: ciudadlatina.net</li>
                </ol>
            `
        },
        {
            text: "¿Cómo conseguir trabajo?",
            answer: `
                <p class="accountmenu__content__cards__answer__text">
                Puedes conseguir un trabajo en el edificio del gobierno en la sección "trabajo".<br><br>
                Los trabajos están disponibles a medida que aumenta el nivel del trabajador. Se puede actualizar ganando experiencia mientras se trabaja. Puede realizar un seguimiento de la progresión de la carrera en las estadísticas.
                <br><br>Lista de trabajos disponibles:
                </p>
                <ol>
                <li class="accountmenu__content__cards__answer__li">Nivel 1 – Manitas, jardinero</li>
                <li class="accountmenu__content__cards__answer__li">Nivel 2 – Conductor de autobús, taxista</li>
                <li class="accountmenu__content__cards__answer__li">Nivel 3 – Cartero, fotógrafo, mecánico</li>
                <li class="accountmenu__content__cards__answer__li">Nivel 4 – Coleccionista, camionero en una furgoneta</li>
                <li class="accountmenu__content__cards__answer__li">Nivel 8 – Conductor de camión</li>
                <li class="accountmenu__content__cards__answer__li">Nivel 12 – Camionero en un camión con remolque</li>
                </ol>
            `
        },
        {
            text: "Dónde comprar licencias?",
            answer: `
                <p class="accountmenu__content__cards__answer__text">
                Las licencias de vehículos se pueden comprar en el centro de licencias ubicado en el edificio del gobierno. Para comprar una licencia comercial, de caza o de pesca, comuníquese con los funcionarios del gobierno.
                <br><br>
                                    Las licencias de armas son emitidas por oficiales de LSPD y BCSD. Pero primero necesitas un seguro médico, que se puede comprar en una farmacia o de los empleados de EMS.
                                </p>
            `
        },
        {
            text: "Dónde comprar tarjetas bancarias?",
            answer: `
                <p class="accountmenu__content__cards__answer__text">
                Las tarjetas bancarias se compran en cualquier sucursal bancaria de Fleeca, Maze, Blaine o Pacific. Entre ellos, solo difieren en la ubicación y la comisión por retirar fondos de un cajero automático. Pero al comienzo del juego, puedes elegir cualquier banco, no te preocupes.
                </p>
            `
        },
        {
            text: "Cómo obtener servicios de donación?",
            answer: `
                <p class="accountmenu__content__cards__answer__text">
                Los servicios de donación se pueden utilizar en la sección "Donar" del sitio web ciudadlatina.net
                </p>
            `
        },
        {
            text: "Cómo denunciar a un jugador o administrador?",
            answer: `
                <p class="accountmenu__content__cards__answer__text">
                Puedes dejar un reporte sobre un jugador o un administrador en el sitio <a>ciudadlatina.net</a> en la sección "Reporte".
                </p>
            `
        },
        {
            text: "Todo sobre el vehículo",
            answer: `
                <p class="accountmenu__content__cards__answer__text">
                Hay 10 espacios para vehículos en total. Puedes tener hasta 5 autos sin casa, los 5 cupos restantes se adquieren en el sitio web <a>ciudadlatina.net</a> en la sección "Donar".
                </p>
                <br><br>
                <h1>Donde comprar un vehículo?</h1><br>
                <p class="accountmenu__content__cards__answer__text">
                Los concesionarios de automóviles están marcados en el mapa con un ícono de garaje con un dólar o en el teléfono en la pestaña GPS. El más barato está en Sandy Shores, donde puedes comprar vehículos hasta $2000
                </p>
                <br><br>
                <h1>Cómo interactuar con el vehículo?</h1><br>
                <p class="accountmenu__content__cards__answer__text">
                Abra el teléfono (por defecto con la tecla O) y ejecute la aplicación UVeh.<br><br>
                Presione L (estándar) para abrir/cerrar el transporte.<br><br>
                Para arrancar/detener el motor, presione B (estándar).
                </p>
                <br><br>
                <h1>Cuanta velocidad me da el tuning de autos?</h1><br>
                <p class="accountmenu__content__cards__answer__text">
                Turbo otorga +10 a la velocidad del vehículo y 4 motores de nivel +20, en total, cuando el automóvil esté completamente actualizado, obtendrá +30 a la velocidad máxima
                </p>
                <br><br>
                <h1>¿Cómo robar el vehículo de otro jugador? Y que debo hacer con el?</h1><br>
                <p class="accountmenu__content__cards__answer__text">
                Puedes robar vehículos usando ganzúas o hackeando la consola. Puede entregar el automóvil a través de la consola usando comandos especiales.
                </p>
            `
        },
        {
            text: "Cómo comprar una casa?",
            answer: `
                <p class="accountmenu__content__cards__answer__text">
                Hay dos formas de comprar una casa:
                </p>
                <ol>
                <li class="accountmenu__content__cards__answer__li">Seleccione la casa deseada y, si está libre, vaya a la entrada de la casa, presione [E] y compre.</li>
                <li class="accountmenu__content__cards__answer__li">Si la casa pertenece a otro jugador, cómprele la vivienda de sus manos por su precio establecido en el edificio del gobierno, en la sección "Propiedad".</li>
            
                </ol>
            `
        },
        {
            text: "Cómo funcionan los impuestos a la propiedad?",
            answer: `
                <p class="accountmenu__content__cards__answer__text">
                Los impuestos se aplican a todo su transporte y alojamiento. Puede obtener información sobre impuestos y pagarlos en el edificio del gobierno, en la sección "Oficina de impuestos".
                </p>
            `
        },
        {
            text: "Cómo acelerar la nivelación de habilidades?",
            answer: `
                <p class="accountmenu__content__cards__answer__text">
                Corre, lucha, dispara o nada con más frecuencia. Si desea actualizar sus habilidades de inmediato, puede comprar el servicio correspondiente en la sección "Donar" del sitio web ciudadlatina.net.
                </p>
            `
        },
        {
            text: "¿Qué es una consola? Cómo conseguirlo y dónde está Lamar?",
            answer: `
                <p class="accountmenu__content__cards__answer__text">
                La consola es una aplicación dedicada a la piratería. Debe obtener la consola de Lamar, que está en el Ghetto (marcado en el mapa como un hombre amarillo).
                </p>
            `
        },
        {
            text: "¿Cómo encontrar mafia, pandilla o cualquier otra organización criminal?",
            answer: `
                <p class="accountmenu__content__cards__answer__text">
                Las pandillas están marcadas con íconos de calaveras en el mapa del Ghetto. Las mafias y organizaciones criminales no oficiales están ocultas en el mapa. Su ubicación se puede encontrar a través de otros jugadores.
                </p>
            `
        },
        {
            text: "¿Qué es la rueda de la fortuna y qué cae allí?",
            answer: `
                <p class="accountmenu__content__cards__answer__text">
                La Rueda de la Fortuna es una rueda de ruleta que se puede jugar todos los días reales después de jugar solo 3 horas en el servidor. La rueda de la fortuna está disponible en el casino, entre las ganancias hay máscaras raras, un auto caro o un VIP.
                </p>
            `
        },
        {
            text: "¿Cómo puedo saciar mi sed y mi hambre?",
            answer: `
                <p class="accountmenu__content__cards__answer__text">
                Compra alimentos y bebidas a las 24/7. Si no satisfaces tus necesidades a tiempo, entonces tu personaje comenzará a caer y eventualmente perderá el conocimiento..
                </p>
            `
        },
        {
            text: "Cómo conseguir un trabajo en cualquier organización del estado?",
            answer: `
                <p class="accountmenu__content__cards__answer__text">
                En total hay 5 organizaciones estatales: LSPD, BCSD, FIB, EMS, USMC, Life Invader y Gobierno. Para conseguir un trabajo en uno de ellos, debe ir al edificio principal de la organización (marcado en el mapa) y contactar a uno de los empleados y averiguar los detalles de la entrada.
                <br>
                <br>
                <ol>
                <li class="accountmenu__content__cards__answer__li">LSPD (Departamento de Policía) - Comprometido con la ley y el orden de nuestra ciudad</li>
                <li class="accountmenu__content__cards__answer__li">BCSD (Departamento del Sheriff) - Manejar la aplicación de la ley del condado de Blaine</li>
                <li class="accountmenu__content__cards__answer__li">GOB (Gobierno) - Financiamiento de las organizaciones estatales, dictámenes y redacción de leyes</li>
                <li class="accountmenu__content__cards__answer__li">EMS (Hospital) - Cuestiones de salud, seguro médico, trata a las personas y elimina los accidentes</li>
                <li class="accountmenu__content__cards__answer__li">FIB (Oficina Federal de Investigaciones) - Controla los órganos estatales y su órden</li>
                <li class="accountmenu__content__cards__answer__li">USMC (Ejército) - Se ocupa del abastecimiento y la seguridad del Estado</li>
                <li class="accountmenu__content__cards__answer__li">Life Invader (Noticias): escribe noticias, presenta eventos y edita anuncios</li>
                </ol>
                </p>
            `
        },
    ]

    const [active, setActive] = useState(0)

    return (
        <React.Fragment>
            <div className="accountmenu__content__cards__item question accountmenu__scrollable with-margin">
                <div className="accountmenu__content__cards__container">
                    <span className="accountmenu__content__cards__title_single">
                        Preguntas frecuentes
                    </span>
                    <div className="accountmenu__cards__question__container">
                        {questions.map((item, index) => (
                            <Question
                                text={item.text}
                                key={index.toString()}
                                index={index}
                                active={active}
                                setActive={() => setActive(index)}
                            />
                        ))}
                    </div>
                </div>
            </div>
            <div className="accountmenu__content__cards__bitem accountmenu__scrollable">
                <div className="accountmenu__content__cards__container answer">
                    <div
                        className="accountmenu__content__cards__answer__img"
                    />
                    <div className="accountmenu__cards__question__icon__container__active">
                        <img src={`https://ciudadlatina.cc/client/images/icons/components/accountmenu/img/questio_icon.svg`} className="accountmenu__cards__question__icon" />
                        <span className="accountmenu__content__cards__answer__title">
                            {questions[active].text}
                        </span>
                    </div>
                    <div
                        className="accountmenu__content__cards__answer__text__container" 
                        dangerouslySetInnerHTML={{__html: questions[active].answer}}
                    />
                </div>
            </div>
        </React.Fragment>
    )
}

export default Faq