import React from 'react';
import EventManager from "../../../EventManager";
import { AttachMoney, MoneySharp } from '@material-ui/icons';
import Draggable from '../Draggable'
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faCreditCard, faMoneyBill, faMoneyCheck } from '@fortawesome/free-solid-svg-icons';

class Money extends React.Component {
    constructor(props) {
        super(props)
        this.state = {
            show: true,
            wallet: "12.400.000$",
            card: "12.400$",
        }
    }

    componentDidCatch(error, errorInfo) {
        mp.trigger('client:ui:debug', 'Money.jsx', error, errorInfo); // eslint-disable-line
    }

    componentDidMount() {
        EventManager.addHandler('hudg', value => {
            if (value.type === 'show') {
                this.setState({ show: true })
            } else if (value.type === 'hide') {
                this.setState({ show: false })
            } else if (value.type === 'updateValues') {
                this.setState({ show: value.isShow });
                this.setState({ wallet: value.wallet });
                this.setState({ card: value.card });
            }
            else return;
        })
    }

    componentWillUnmount() {
        EventManager.removeHandler('hudg');
    }

    render() {
        if (!this.state.show) {
            return null;
        }
        return (
            <React.Fragment>

                <Draggable id="money" className="money-main">
                    
                    <div className='money-contain'>
                        <div className="money-title"><FontAwesomeIcon icon={faMoneyBill} className='hud-icon' />{this.state.wallet}</div>
                        <div className="money-txt" ><FontAwesomeIcon icon={faCreditCard} className='hud-icon' />{this.state.card}</div>
                    </div>

                </Draggable>
            </React.Fragment>
        )
    }
}

export default Money;
