import React from 'react';
import EventManager from "../../../EventManager";
import Draggable from '../Draggable'
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faUtensils, faGlassWhiskey, faMoneyBill, faCreditCard } from '@fortawesome/free-solid-svg-icons';
import { CircularProgressbar } from 'react-circular-progressbar';
import { buildStyles } from 'react-circular-progressbar';
import { MicOff, Mic } from '@material-ui/icons';
import { green } from '@material-ui/core/colors';
import 'react-circular-progressbar/dist/styles.css';

class Player extends React.Component {
    constructor(props) {
        super(props)
        this.state = {
            show: true,
            microphone: false,
            drink: 18,
            eat: 60,
            wallet: "12.400.000$",
            card: "12.400$",
            color: '#48B9F2',
            background: 0.5,
        }
    }

    componentDidCatch(error, errorInfo) {
        mp.trigger('client:ui:debug', 'Player.jsx', error, errorInfo); // eslint-disable-line
    }

    componentDidMount() {
        EventManager.addHandler('hudp', value => {
            if (value.type === 'show') {
                this.setState({ show: true })
            } else if (value.type === 'hide') {
                this.setState({ show: false })
            } else if (value.type === 'updateValues') {
                this.setState({ microphone: value.microphone });
                this.setState({ drink: value.drink });
                this.setState({ eat: value.eat });
                this.setState({ wallet: value.wallet });
                this.setState({ card: value.card });
                this.setState({ background: value.background });
            } else return;
        })
    }

    componentWillUnmount() {
        EventManager.removeHandler('hudp');
    }

    formatCurrency(num) {
        return num.toString().replace(/(\d)(?=(\d{3})+(?!\d))/g, '$1 ')
    }

    render() {
        if (!this.state.show) {
            return null;
        }
        return (
            <React.Fragment>
                <div className="player-hud">
                    <div className="player-hud__box">
                        <div className="player-hud__box__row">
                            <Draggable id="player-money" className="phud-money">
                                <h1 className='title-needs'>Jugador</h1>
                                <div className='player-container'>
                                    <div className='money-container'>
                                        <div className="money-title"><FontAwesomeIcon icon={faMoneyBill} className='hud-icon' />{this.state.wallet}</div>
                                        <div className="money-txt" ><FontAwesomeIcon icon={faCreditCard} className='hud-icon' />{this.state.card}</div>

                                    </div>
                                    <Draggable id="player-mic" className="phud-mic" style={{ backgroundColor: 'rgba(30, 30, 30, ' + this.state.background + ')' }}>
                                       {this.state.microphone ? <Mic style={{ color: '#2ffb59' }}/> : <MicOff />}
                                    </Draggable>
                                </div>
                            </Draggable>
                        </div>
                        <Draggable id="player-needs" className="phud-needs">
                            <h1 className='title-needs'>Necesidades</h1>
                            <div className="needs-box">
                                <div className='circular-progress-drink'>
                                    <CircularProgressbar value={this.state.drink}
                                        styles={buildStyles(this.state.drink <= 20 ? {
                                            pathColor: `#ff0000`,
                                            trailColor: '#Ebeceb',
                                        } : {
                                            pathColor: `#30be25`,
                                            trailColor: '#Ebeceb'
                                        })}
                                    />
                                    <FontAwesomeIcon icon={faGlassWhiskey} size="lg" className={this.state.drink <= 20 ? "color-icon-drink-low" : "color-icon-drink"} />
                                </div>

                                <div className='circular-progress-eat'>
                                    <CircularProgressbar value={this.state.eat}
                                        styles={buildStyles(this.state.eat <= 20 ? {
                                            pathColor: `#ff0000`,
                                            trailColor: '#Ebeceb',
                                        } : {
                                            pathColor: `#30be25`,
                                            trailColor: '#Ebeceb'
                                        })} />
                                    <FontAwesomeIcon icon={faUtensils} size="lg" className={this.state.eat <= 20 ? "color-icon-drink-low" : "color-icon-drink"} />
                                </div>
                            </div>


                        </Draggable>
                    </div>
                </div>
            </React.Fragment>
        )
    }
}

export default Player;
